export default {
	data(){
		return{
			specClass: 'none',
			scrollTop:0,
			navnarwidth:0,
			windowHeight:0,
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			isPlayVideo:true,
			videoDirection:0,
			showPlayBtn:true,
			showFullscreenBtn:true,
			// require('../../static/happy.mp4')
			// https://mp4.vjshi.com/2017-06-17/ed1d63669bea39f5ef078c4e194291d6.mp4
			videoSrc:'https://mp4.vjshi.com/2017-06-17/ed1d63669bea39f5ef078c4e194291d6.mp4',
			pronumber:1
		}
	},
	onReady: function (res) {
		this.videoContext = uni.createVideoContext('myVideo')
	},
	onShow() {
		var _that = this
		wx.getSystemInfo({
		  success: function(res) {
			  console.log(res.statusBarHeight)
			_that.navnarHeight = res.statusBarHeight
			_that.windowHeight = res.windowHeight
		  }
		})
	},
	methods:{
		upper(){
			
		},
		lower(){
		},
		scroll(){
		
		},
		
		//购买商品
		// buy(){
			
		// },
		//路由跳转
		buy(url){
			// if(!this.hasLogin){
			// 	url = '/pages/public/login';
			// }
			uni.navigateTo({  
				url
			})  
		}, 
		
		toChat(url){
			uni.navigateTo({
				url
			})  
		},	
		goallmassgae(url){
			uni.navigateTo({
				url
			})
		},
		
		
		//规格弹窗开关
		toggleSpec() {
			if(this.specClass === 'show'){
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			}else if(this.specClass === 'none'){
				this.specClass = 'show';
			}
		},
		
		//展示图片
		ShowImage(){
			let src = 'https://ae01.alicdn.com/kf/HTB1wREwTXzqK1RjSZFvq6AB7VXaT.jpg';
			let srclist = [
				'https://ae01.alicdn.com/kf/HTB1wREwTXzqK1RjSZFvq6AB7VXaT.jpg',
				'https://ae01.alicdn.com/kf/HTB1wREwTXzqK1RjSZFvq6AB7VXaT.jpg',
				'https://ae01.alicdn.com/kf/HTB1wREwTXzqK1RjSZFvq6AB7VXaT.jpg'
			]
			uni.previewImage({
				current:src,
				urls: srclist
			});
		},
		
		
		stopPrevent(){},
		
		
		//点击播放视频
		playVideo() {
			console.log(this.videoSrc)
			// this.isPlayVideo = true;
			this.$nextTick(function() {
				this.videoContext.requestFullScreen({direction:0});
			});
			
		},
		stopPlayVideo(){
			this.videoSrc = '';
			this.videoContext.pause();
		},
		
		//@fullscreenchange
		viderFullscreen(e){
			if(e.detail.fullScreen){
				this.videoContext.play();
			}else{
				this.stopPlayVideo();
			}
		},
		//视频暂停
		videoPause(){
			// this.isPlayVideo = false;
			// this.videoSrc = '';
		}, 
		
		
		//分享商品
		onShareAppMessage(res){
			console.log(res)
			if (res.from === 'button') {
			  // 来自页面内转发按钮
			  console.log(res.target)
			}
			return {
			  title: '自定义转发标题',
			  path: '/pages/goods/goods?id=123',
			  imageUrl:'../../static/img/face.jpg'
			}
		}
	}
}
